import React from "react";
import {Link} from "react-router-dom";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
        }
    }

    handleClick() {
        this.setState(state => ({
            growText: !state.growText
        }));
    }

    render() {
        return (

            <div className={"box"}>
                <div className={"mainbodyz"}>
                    <div className={"boxz"}>
                        <div className={"boxzin"}>
                            <h1>About Us: </h1>
                            <p>Arctic Shipping Corporation has been in the industry
                                for almost 28 years. Established and incorporated in April 1991,
                                Arctic has provided courteous and competent seafarers for more than 20 ocean-going
                                vessels. As a manning agency, the company provides full crewing to principals - Nissen
                                Kaiun Co., Ltd. and their group of companies.
                            </p>
                            <p>In 1991, five colleagues joined together to form
                                Arctic Shipping Corporation with a goal to participate in the maritime business by
                                enlisting Filipino seamen as crew to both local and international ships. The founders
                                of the company have had extensive experience in the shipping industry. The board of
                                directors has also ventured into ship-owning and management of vessels, before settling
                                into the manning and crewing business with the formation of Arctic.</p>
                            <p>What started out as a minor venture has grown into a stable and secure organization that
                                has withstood the years. In 2003, Arctic was cited by the Philippine Overseas Employment
                                Administration (POEA) as one of the outstanding manning agencies in the country. Today,
                                Arctic continues to train potential seamen and develop them into able and proficient
                                officers that quickly advance through the ranks..</p>
                        </div>
                        <div className={"displayz"}>
                            <img src= {require('./assets/bowats.png')} alt={"Title"} width={"500"} height={"700"}/>
                        </div>
                    </div>
                </div>
            </div>


        )
    }
}
export default Home;