import React from "react";

const Gallery = () => {
    return (
        <div className="box">
            <div>
                <h1>Gallery</h1>
                <p>Our fleets</p>
            </div>
            <Images />
        </div>
    )
};

class Images extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            imgid: 0,
            gallery: [require('./assets/0.png'),
                    require('./assets/1.png'),
                    require('./assets/3.png'),
                    require('./assets/4.png'),
                    require('./assets/5.png'),
                    require('./assets/6.png'),
                    require('./assets/7.png'),
                    require('./assets/8.png'),],
            desc: ["M.V. King Ore",
                    "M.V. Bulk Denmark",
                    "M.V. Alam Sayang",
                    "M.V. Eibhlin",
                    "M.V. Clover",
                    "M.V. Mount Hope",
                    "M.V. Topaz Halo",
                    "M.V. Cologny (ex-Trinity Star)"],
        };
    }

    loop = () => {
        return this.state.gallery.map((element, index) => {
            return (
                <div key={index} onClick={() => this.handleClick(index)}>
                    {this.renderImg(index)}
                </div>
            )
        })
    };

    renderImg(i){
        return(
            <img src= {this.state.gallery[i]}
                 id={[i]}
                 value={[i]}
                 className={"maingallery"}
                 width={"680px"}
                 height={"380px"}
                 alt={"Sub Img"}/>
        )
    }

    renderMainImg(){
        const x = this.state.imgid;
        return(
            <div>
                <img src= {this.state.gallery[x]}
                     id={[x]}
                     value={[x]}
                     className={"maingallery"}
                     width={"680px"}
                     height={"380px"}
                     alt={"Main Img"}/>
                     <p>{this.state.desc[x]}</p>
            </div>
        )
    }

    handleClick(i){
        this.setState({
            imgid: i,
        });
    }

    render() {
        return (
            <div>
                <div>
                    {this.renderMainImg()}
                </div>
                <div className={"flex"}
                     id={"subgallery"}>
                    {this.loop()}
                </div>
            </div>
        )
    }
}


export default Gallery;